body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
  font-family: 'Gothic A1', sans-serif;
  font-weight: 300;
}

.btn-outline-light{
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  border-radius: 10px;
  display: inline-block;
  font-size: 20px;
  margin: 10px;
}

h1{
  font-size: calc(120px + 2vmin);
}

.App-header {
  background-image: linear-gradient(to top, #249494 0%, #330867 100%);
  background-size: 180% 180%;
  -webkit-animation: gradient-animation 18s ease infinite;
          animation: gradient-animation 18s ease infinite;
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
}

.App-body {
  font-size: calc(13px + 2vmin);
  text-align: left;
  padding-top: 0px; 
  padding-bottom: 0px; 
}

.App-bottom {
  font-size: calc(1px + 1vmin);
  text-align: center;
  position: absolute;
  bottom: 10px
}

.App-link {
  color: #61dafb;
}

.transform{
  transition: 0.5s ease-in-out;
}

.transform:hover{
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.circle{
  width: calc(500px + 1vmin);
  height: calc(500px + 1vmin);
  border-radius: 50%;
  border: none;
  background: slateblue;
  color: white;
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
}

@-webkit-keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.h1{
  font-size: calc(100px + 2vmin);
}

.FilmsWatched-Header {
  background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(30px + 2vmin);
  color: white;
}

.title{
  font-size: calc(60px + 2vmin);
}

.FilmsWatchedBody {
  font-size: calc(13px + 2vmin);
  text-align: left;
}

.genre-loading {
  opacity: 1;
  -webkit-animation: fade 2s ease-in-out;
          animation: fade 2s ease-in-out;
}

@-webkit-keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}

@keyframes fade {
  0%,100% { opacity: 0 }
  50% { opacity: 1 }
}
.fade-in-animation {
    opacity: 1;
    -webkit-animation: fade-in 0.5s ease-in;
            animation: fade-in 0.5s ease-in;
  }
  
  @-webkit-keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
  
  @keyframes fade-in {
    0% { opacity: 0 }
    100% { opacity: 1}
  }
